import React, { Component } from "react";
import { connect } from "react-redux";

import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import StarBorder from "@material-ui/icons/StarBorder";

import {useHistory, useLocation} from 'react-router'

const useStyles = makeStyles((theme) => ({
  root: {
      flexBasis:"300px",
      flexGrow:0,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

interface Props {
  isMobile: Boolean,
  cities:[]
}

export const sideBarContainer: React.FC<Props> = (props) => {
  const classes = useStyles();
  const history = useHistory();
  const location = useLocation();
  console.log(location)
  const [open, setOpen] = React.useState<boolean>(true);
  const [selected, setSelected] = React.useState<null | number>(null);
  const handleClick = () => {
    setOpen(!open);
  };

  const selectedCityHandler = (index) => {
      const path = `/forecast/${props.cities[index]}`;
      setSelected(index);
      history.push(path);
  }

  return (
    <div className={classes.root}>
      <List
        component="nav"
        aria-labelledby="nested-list-subheader"
      >
        <ListItem button onClick={handleClick}>
          <ListItemIcon>
            <InboxIcon />
          </ListItemIcon>
          <ListItemText primary="Locations" />
          {open ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={open} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            {props.cities.map((city, index) => {
              return (
                <ListItem button className={classes.nested} selected={selected === index} onClick={()=>selectedCityHandler(index)}>
                  <ListItemIcon>
                    <StarBorder />
                  </ListItemIcon>
                  <ListItemText primary={city} />
                </ListItem>
              );
            })}
          </List>
        </Collapse>
      </List>
    </div>
  );
};

const mapStateToProps = ({cityReducer}) => ({
    cities:cityReducer.cities
});

const mapDispatchToProps = {};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(sideBarContainer);
