import React from "react";

import { connect } from "react-redux";
import { Switch, Route, Redirect } from "react-router-dom";

import UserProfile from "../Components/user/userProfile";
import EditProfile from "../Components/user/editProfile";
import Login from "../Components/login/login";
interface Props {
  isAuth: boolean,
  user:{ displayName: any; photoURL: any; email: any; phoneNumber: any; }
}
function PrivateRoute({ isAuth, children, ...rest }) {
  return (
    <Route
      {...rest}
      render={({ location }) =>
        isAuth ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/user/login",
              state: { from: location },
            }}
          />
        )
      }
    />
  );
}

function userContainer<Props>({ isAuth, user }) {
  return (
    <Switch>
      <Route path="/user" exact>
        <Redirect to="/user/profile" />
      </Route>
      <PrivateRoute isAuth={isAuth}  path="/user/profile" exact>
        <UserProfile user={user} />
      </PrivateRoute>
      <PrivateRoute isAuth={isAuth}  path="/user/profile/edit">
        <EditProfile user={user} />
      </PrivateRoute>
      <Route path="/user/login">
        {isAuth ? <Redirect to="/weather" /> : <Login /> }
      </Route>

      <Route path="*">
        <h1>not found</h1>
      </Route>
    </Switch>
  );
}

const mapStateToProps = ({ userReducer }) => {
  return {
    isAuth: userReducer.user !== null,
    user:userReducer.user
  };
};

export default connect(mapStateToProps, null)(userContainer);
