import React from "react";
import {
  Paper,
  Avatar,
  Card,
  CardActions,
  CardContent,
  Typography,
  Button,
  CircularProgress
} from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles(theme => {
  return {
    root: {},
    Header: {
      height: "100px",
      display: "flex",
      justifyContent: "center"
    },
    large: {
      width: theme.spacing(10),
      height: theme.spacing(10)
    }
  };
});

export default function userProfile(props) {
  const { displayName, photoURL, email, phoneNumber } = props.user;
  const classes = useStyles();
  const history = useHistory();
  const [redirect, setredirect] = React.useState(false);
  const editProfile = () => {
    setredirect(true);
    setTimeout(() => {
      history.push("/user/profile/edit");
    }, 500);
  };
  return (
    <div className={classes.root}>
      <div className={classes.Header}>
        <Avatar
          alt="Remy Sharp"
          src="https://cdn5.vectorstock.com/i/1000x1000/66/14/default-avatar-photo-placeholder-profile-picture-vector-21806614.jpg"
          className={classes.large}
        />
      </div>
      <Card>
        <CardContent>
          <div>
            <p>
              <strong>User Name:</strong>
              
                <Typography variant="button" display="block" gutterBottom>
                {displayName ? 
                displayName : "Add your user name"}
                </Typography>
            </p>
          </div>
          <div>
            <p>
              <strong>Email:</strong>
                <Typography variant="button" display="block" gutterBottom>
                {email ? email : "Add your email"}
                </Typography>
            </p>
          </div>
          <div>
            <p>
              <strong>Phone number:</strong>
              <Typography variant="button" display="block" gutterBottom>
                {phoneNumber ? phoneNumber : "Add your phone number"}
              </Typography>
            </p>
          </div>
        </CardContent>
        <CardActions>
          <Button disabled={redirect} size="small" onClick={editProfile}>
            {redirect ? <CircularProgress size="1rem" /> : "Edit Profile"}
          </Button>
        </CardActions>
      </Card>
    </div>
  );
}
